import {
  createWebHistory,
  createRouter
} from "vue-router";
import Visual from "@/pages/Main/index.vue"
import About from "@/pages/About/index.vue"
import Work from "@/pages/Work/index.vue"
import Contact from "@/pages/Contact/index.vue"

const routes = [{
    path: "/",
    component: Visual,
    name: 'Visual'
  },
  {
    path: "/about",
    component: About,
    name: 'About'
  },
  {
    path: "/work",
    component: Work,
    name: 'Work'
  }, {
    path: "/contact",
    component: Contact,
    name: 'Contact'
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;